.main-p{
    display: block;
    padding: 20px;
    padding-bottom: 5%;
}

.logo-p{
   

    width: 84px;
    position: absolute; 
    left: 50%;
    margin-left: -42px;
    top:80px;

}

.text-div-p{
    text-align: center;
    display: flex;
    
    flex-direction: column;
    padding: 10px;
    gap: 12px;

    position: absolute;
    left: 5%;
    right: 5%;
    top: 180px;
}

.header-p{


    text-overflow: ellipsis;
    overflow: hidden;
    -ms-line-clamp: 1;
    -webkit-line-clamp: 1;
    line-clamp: 1;
   
    

    font-family: 'SF Pro Display';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    /* identical to box height */

    text-align: center;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.colortext-p{
    font-family: 'SF Pro Display';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    /* identical to box height */

    text-align: center;
    letter-spacing: 0.0025em;

    color: #51A0EF;


    /* Inside auto layout */

    flex: none;
    order: 1;
    flex-grow: 0;
}


